<template>
  <div class="payPurpose">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>您现在的位置：服务管理</el-breadcrumb-item>
        <el-breadcrumb-item>个性设置</el-breadcrumb-item>
        <el-breadcrumb-item>用途维护</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div v-if="active === 3" style="padding: 20px 0">
      <el-form ref="form" :model="addForm" label-width="200px" style="width: 600px">
        <el-form-item label="用途类型：">
          <el-select v-model="addForm.userFul" style="width:260px;">
            <el-option label="收付款用途" value></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button
            size="mini"
            type="primary"
            style="background-color: #1f93d5;border-color: #1f93d5;"
            @click="searchInfo"
          >查询</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div v-if="active === 1" style="padding: 20px 0">
      <div>
        <el-table :data="purposeList" border highlight-current-row fit size="mini" @selection-change="multiSelect">
          <el-table-column type="selection" align="center"></el-table-column>
          <el-table-column label="名称" prop="purpose" align="center"></el-table-column>
        </el-table>
      </div>
      <div class="text-center mt-15">
        <el-button
          type="primary"
          style="background-color: #1f93d5;border-color: #1f93d5;"
          size="mini"
          @click="active = 3"
        >上一步</el-button>
        <el-button
          type="primary"
          style="background-color: #1f93d5;border-color: #1f93d5;"
          size="mini"
          @click="active = 2"
        >新增</el-button>
        <el-button
          type="primary"
          style="background-color: #1f93d5;border-color: #1f93d5;"
          size="mini"
          @click="modInfo"
        >修改</el-button>
        <el-button
          type="primary"
          style="background-color: #1f93d5;border-color: #1f93d5;"
          size="mini"
          @click="del"
        >删除</el-button>
      </div>
    </div>
    <div v-else-if="active === 2" class="p-20">
      <div class="pay-transfer-header mb-20 mt-15">收付款用途</div>
      <el-form ref="form" :model="addForm" label-width="200px" style="width: 1000px;height:210px;">
        <el-form-item label="自定义用途1：" style="float:left">
          <el-input v-model="addForm.purpose1" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途2：" style="float:right">
          <el-input v-model="addForm.purpose2" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途3：" style="float:left">
          <el-input v-model="addForm.purpose3" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途4：" style="float:right">
          <el-input v-model="addForm.purpose4" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途5：" style="float:left">
          <el-input v-model="addForm.purpose5" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途6：" style="float:right">
          <el-input v-model="addForm.purpose6" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途7：" style="float:left">
          <el-input v-model="addForm.purpose7" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途8：" style="float:right">
          <el-input v-model="addForm.purpose8" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途9：" style="float:left">
          <el-input v-model="addForm.purpose9" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="自定义用途10：" style="float:right">
          <el-input v-model="addForm.purpose10" style="width: 200px"></el-input>
        </el-form-item>
      </el-form>

      <div class="pay-transfer-header mb-20 mt-15">请输入认证信息</div>
      <el-form
        ref="pwdForm"
        :model="pwdForm"
        :rules="rule"
        label-width="200px"
        style="width: 1000px"
      >
        <el-form-item label="交易密码：" prop="pwd">
          <el-input v-model="pwdForm.pwd" style="width: 200px" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="mini"
            style="background-color: #1f93d5;border-color: #1f93d5;"
            @click="active = 1"
          >上一步</el-button>
          <el-button
            type="primary"
            size="mini"
            style="background-color: #1f93d5;border-color: #1f93d5;"
            @click="save"
          >确定</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div v-else-if="active === 4" class="p-20">
      <el-table :data="multiSelection" border highlight-current-row fit size="mini" :row-class-name="rowClassName">
        <el-table-column prop="id" label="序号" width="80" align="center"></el-table-column>
        <el-table-column label="名称" prop="purpose" align="center">
          <template slot-scope="{row}">
            <el-input v-model="row.purpose" style="width: 200px"></el-input>
          </template>
        </el-table-column>
      </el-table>

      <div class="pay-transfer-header mb-20 mt-15">请输入认证信息</div>
      <el-form ref="pwdModForm" :model="pwdModForm" :rules="rule" label-width="200px" style="width: 1000px">
        <el-form-item label="交易密码：" prop="pwd">
          <el-input v-model="pwdModForm.pwd" style="width: 200px" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="mini"
            style="background-color: #1f93d5;border-color: #1f93d5;"
            @click="modBack"
          >上一步</el-button>
          <el-button
            type="primary"
            size="mini"
            style="background-color: #1f93d5;border-color: #1f93d5;"
            @click="saveInfo"
          >确定</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PayPurpose',
  data () {
    return {
      multiSelection: [],
      active: 3,
      addForm: {
        purpose1: '',
        purpose2: '',
        purpose3: '',
        purpose4: '',
        purpose5: '',
        purpose6: '',
        purpose7: '',
        purpose8: '',
        purpose9: '',
        purpose10: '',
        userFul: ''
      },
      rule: {
        pwd: [{ required: true, message: '请填密码', trigger: 'blur' }]
      },
      pwdForm: {
        pwd: ''
      },
      pwdModForm: {
        pwd: ''
      },
      number: 0,
      list: []
    }
  },
  computed: {
    purposeList () {
      return this.$store.state.purposeList
    }
  },
  created () {

  },
  methods: {
    modBack () {
      this.active = 1
      this.multiSelection = []
    },
    rowClassName ({ row, rowIndex }) {
      // 把每一行的索引放进row.id
      row.id = rowIndex + 1
    },
    modInfo () {
      if (this.multiSelection.length === 0) {
        this.$message.error('请选择修改数据')
      } else if (this.multiSelection.length > 1) {
        this.$message.error('一次只能修改一条数据')
      } else {
        this.active = 4
      }
    },
    searchInfo () {
      this.active = 1
    },
    multiSelect (value) {
      this.multiSelection = value
    },
    // 删除
    del () {
      if (this.multiSelection.length <= 0) {
        this.$message.warning('请选择要删除的用途')
      } else {
        this.$confirm('此操作将删除用途, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          const data = []
          this.multiSelection.forEach(v => {
            data.push(v.purposeId)
          })
          this.$store.commit('delPurpose', data)
          this.$message.success('删除成功')
        }).catch(() => {
          console.log('cancel')
        })
      }
    },
    save () {
      if (!this.addForm.purpose1 && !this.addForm.purpose2 && !this.addForm.purpose3 && !this.addForm.purpose4 &&
        !this.addForm.purpose5 && !this.addForm.purpose6 &&
        !this.addForm.purpose7 && !this.addForm.purpose8 && !this.addForm.purpose9 && !this.addForm.purpose10) {
        this.$message.warning('请填写用途名称')
        return false
      }
      const _v = []
      if (this.addForm.purpose1) {
        _v.push({ purpose: this.addForm.purpose1, purposeId: new Date().getTime() })
      }
      if (this.addForm.purpose2) {
        _v.push({ purpose: this.addForm.purpose2, purposeId: new Date().getTime() + 1 })
      }
      if (this.addForm.purpose3) {
        _v.push({ purpose: this.addForm.purpose3, purposeId: new Date().getTime() + 2 })
      }
      if (this.addForm.purpose4) {
        _v.push({ purpose: this.addForm.purpose4, purposeId: new Date().getTime() + 3 })
      }
      if (this.addForm.purpose5) {
        _v.push({ purpose: this.addForm.purpose5, purposeId: new Date().getTime() + 4 })
      }
      if (this.addForm.purpose6) {
        _v.push({ purpose: this.addForm.purpose6, purposeId: new Date().getTime() + 5 })
      }
      if (this.addForm.purpose7) {
        _v.push({ purpose: this.addForm.purpose7, purposeId: new Date().getTime() + 6 })
      }
      if (this.addForm.purpose8) {
        _v.push({ purpose: this.addForm.purpose8, purposeId: new Date().getTime() + 7 })
      }
      if (this.addForm.purpose9) {
        _v.push({ purpose: this.addForm.purpose9, purposeId: new Date().getTime() + 8 })
      }
      if (this.addForm.purpose10) {
        _v.push({ purpose: this.addForm.purpose10, purposeId: new Date().getTime() + 9 })
      }
      const data = [..._v]
      let bool = true
      if (this.purposeList.length > 0) {
        this.purposeList.forEach(v => {
          if (v.purpose === data.purpose) {
            bool = false
          }
        })
      }
      if (!bool) {
        this.$message.warning('用途名称不允许重复')
        return false
      }
      if (data.id >= 0) {
        // 编辑
        this.$store.commit('editPurposeInfo', data)
        this.$message.success('编辑成功')
        this.resetForm()
        this.active = 1
      } else {
        // 新增
        this.$refs.pwdForm.validate((valid) => {
          if (valid) {
            if (this.pwdForm.pwd === this.$store.state.userInfo.password) {
              this.$store.commit('savePurposeInfo', data)
              this.$message.success('新增成功')
              this.resetForm()
              this.active = 1
            } else {
              this.$message.error('密码错误')
            }
          }
        })
      }
    },
    saveInfo () {
      this.$refs.pwdModForm.validate((valid) => {
        if (valid) {
          if (this.pwdModForm.pwd === this.$store.state.userInfo.password) {
            this.$store.commit('editPurposeInfo', this.multiSelection[0])
            this.$message.success('修改成功')
            this.active = 1
            this.multiSelection = []
          } else {
            this.$message.error('密码错误')
          }
        }
      })
    },
    resetForm () {
      this.addForm = {
        purpose: null
      }

      this.pwdForm.pwd = ''
      this.pwdModForm.pwd = ''
    },
    change (scope) {
      this.addForm = { ...scope.row }
      this.addForm.id = scope.$index
      this.active = 2
      this.calLength()
    },
    cancel () {
      this.addForm.purpose = null
      this.active = 1
    },
    calLength () {
      let len = 0
      for (let i = 0; i < this.addForm.purpose.length; i++) {
        const c = this.addForm.purpose.charCodeAt(i)
        // 单字节加1
        if ((c >= 0x0001 && c <= 0x007e) || (c >= 0xff60 && c <= 0xff9f)) {
          len++
        } else {
          len += 2
        }
      }
      this.number = len
    }
  }
}
</script>

<style lang="scss" scoped>
.payPurpose {
  .header {
    padding: 20px 10px;
    font-size: 14px;
     /deep/ .el-breadcrumb__inner{
      font-size: 16px;
    }
  }
  .pay-purpose-header {
    background-color: #f8f8f8;
    padding: 5px 10px;
  }
}
</style>
